.product {
    padding-top: 50px;
}

.product__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.product__title{
    font-size: 36px;
    color: #1D1D1D;
    line-height: 29px;
    font-weight: 800;
    margin-bottom: 40px;
}
.product__price{
    font-size: 36px;
    color: #1D1D1D;
    line-height: 29px;
    font-weight: 800;
    margin-bottom: 10px;
    display: inline-block;
}
.product__descr{
    font-weight: 400;
    font-size: 14px;
    line-height: 23px;
    color: #1D1D1D;
    margin-bottom: 20px;
}
.product__buttons{
    display: flex;
}